*,
*::after,
*::before {
  box-sizing: border-box;
}

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  width: 100%;
  height: 100%;
}

.page__body {
  margin: 0;
  height: 100%;
  min-width: 320px;
  min-height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 24px;
}

@media (min-width: 1150px) {
  .main-index__contacts {
    inline-size: 1050px;
    margin-inline: auto;
  }
}

.main-header {
  background: linear-gradient(#ffffff 88px, #f9f9f9 88px);
  position: relative;
}
@media (min-width: 768px) {
  .main-header::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    background-color: rgba(0, 0, 0, 0.1);
    top: 88px;
    left: 0;
  }
}

.main-header--inner {
  background: linear-gradient(#ffffff 88px, #f1f1f1 88px);
}

.main-header__container {
  padding: 19px;
}
@media (max-width: 767.98px) {
  .main-header__container {
    border-bottom: solid 1px rgba(0, 0, 0, 0.1);
  }
}
@media (min-width: 768px) {
  .main-header__container {
    padding: 0 50px 0 49px;
  }
}
@media (min-width: 1150px) {
  .main-header__container {
    inline-size: 1050px;
    margin-inline: auto;
    padding: 0;
  }
}

@media (max-width: 767.98px) {
  .main-header__container--nojs {
    padding: 0;
  }
  .main-header__container--nojs .main-header__toggle-navigation {
    display: none;
  }
  .main-header__container--nojs .main-navigation {
    flex-direction: column;
  }
  .main-header__container--nojs .main-navigation .main-navigation__wrapper {
    position: relative;
    top: 0;
  }
  .main-header__container--nojs .main-navigation .main-header__logo {
    align-self: center;
    padding: 22px 0;
    height: auto;
  }
}

.main-header__logo {
  width: 90px;
  height: 40px;
  color: #000000;
}
.main-header__logo:hover {
  opacity: 0.6;
}
.main-header__logo:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-header__logo {
    width: 113px;
    height: 46px;
    align-self: flex-start;
    padding-top: 22px;
    margin-right: 50px;
    grid-column: 1/2;
  }
}
