    width: 149px;
    height: 72px;
    grid-column: 2/3;
    padding-top: 9px;
    margin-bottom: 7px;
  }
}
.main-header__logo[aria-current=page] {
  pointer-events: none;
}

.main-header__toggle-navigation {
  position: absolute;
  cursor: pointer;
  top: 15px;
  right: 20px;
  z-index: 2;
  display: block;
  width: 40px;
  height: 40px;
  background-color: #ffffff;
  border: none;
}
.main-header__toggle-navigation:hover {
  opacity: 0.6;
}
.main-header__toggle-navigation:active {
  opacity: 0.3;
}
.main-header__toggle-navigation::before {
  content: "";
  position: absolute;
  top: 17px;
  right: 11px;
  width: 20px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
}
@media (min-width: 768px) {
  .main-header__toggle-navigation {
    display: none;
  }
}

.main-navigation {
  display: flex;
  justify-content: center;
}
@media (min-width: 768px) {
  .main-navigation {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 1150px) {
  .main-navigation {
    display: grid;
    grid-template-columns: 1fr 152px 1fr;
  }
}

@media (max-width: 767.98px) {
  .main-navigation--opened .main-navigation__wrapper {
    display: block;
    position: absolute;
    top: 79px;
    background-color: #ffffff;
    width: 100%;
    z-index: 1;
  }
  .main-navigation--opened + .main-header__toggle-navigation {
    top: 19px;
    right: 15px;
  }
  .main-navigation--opened + .main-header__toggle-navigation::before, .main-navigation--opened + .main-header__toggle-navigation::after {
    content: "";
    left: 0;
    top: 50%;
    position: absolute;
    width: 28px;
    height: 2px;
    background-color: #000000;
  }
  .main-navigation--opened + .main-header__toggle-navigation::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-navigation--opened + .main-header__toggle-navigation::after {
    transform: rotate(-45deg);
  }
}

.main-navigation__wrapper {
  display: none;
}
@media (min-width: 768px) {
  .main-navigation__wrapper {
    display: contents;
  }
}

